.SelectInput {
	display: inline-block;
	margin: 10px;
}

.SelectInput label {
	text-transform: capitalize;
	font-size: 0.9rem;
	color: grey;
}

.SelectInput select {
	margin: 4px;
	font-size: 1.2rem;
	text-transform: capitalize;
	background: transparent;
	border: none;
}

@media (min-width: 480px) {
	.SelectInput {
		display: -ms-flexbox;
		display: flex;
		-ms-flex-flow: column;
		    flex-flow: column;
		-ms-flex-pack: center;
		    justify-content: center;
		-ms-flex-align: start;
		    align-items: flex-start;
		-ms-flex-line-pack: start;
		    align-content: flex-start;
	}
}



