.TextInput {
	margin: 10px;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-flow: row;
	    flex-flow: row;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: start;
	    align-items: flex-start;
	-ms-flex-line-pack: start;
	    align-content: flex-start;
}

.TextInput label {
	text-transform: capitalize;
	font-size: 0.9rem;
	color: grey;
}

.TextInput input {
	padding: 4px;
	font-size: 1.2rem;
	border-top: none;
	border-right: none;
	border-bottom: 1px solid;
	border-left: none;
}

@media (min-width: 320px) {
	.TextInput {
		-ms-flex-flow: column;
		    flex-flow: column;
		-ms-flex-align: start;
		    align-items: flex-start;
	}
}



